}
.mainButton {
    float: right;
    border: none;
    background-color: #5b5b5b;
    color: white;
    width: 102px;
    height: 36px;
    cursor: pointer;
}
.MainMenu {
    margin-right: 19px;
    margin-bottom: 60px;
    img {
        width: 145px;
        height: 145px;
    }
    div {
        text-decoration-line: none;
