.formBox {
    margin-bottom: 105px;
    width: 75%;
    display: inline-block;
    border-left: 1px solid #e8e8e8;
    padding-left: 7%;
}
.classify {
    margin: 45px 0px;
    font-size: xx-large;
    font-weight: bolder;
}
.essential {
    width: 100%;
    text-align: right;
    padding: 0px 0px 23px 0px;
    border-bottom: 2px solid black;
    font-size: small;
}
.dataBox {
    padding: 30px 20px 5px 20px;
    border-bottom: 1px solid grey;
    .dataTitle {
        width: 20%;
        margin-top: 8px;
        display: inline-block;
        vertical-align: top;
    }
    .dataInputBox {
        display: inline-block;
        width: 80%;
        input {
            padding: 10px;
        }
    }
}
.error {
    color: red;
    font-size: small;
    z-index: 0;
    margin-top: 10px;
    height: 18px;
}
.noErr {
    color: rgba(0, 0, 0, 0);
}
.dataPolicy {
    border-top: 2px solid black;
    padding-top: 20px;
    margin-bottom: 30px;
    table {
        border-collapse: collapse;
        td {
            padding: 20px 0px;
            padding-right: 27px;
            margin: 0px 20px;
            border: 1px solid black;
        }
    }
    div {
        margin: 15px 0px;
    }
    span {
        font-size: medium;
    }
}
.oneInput {
    width: 80%;
    input {
        width: 95%;
    }
}
.contentInput {
    height: 200px;
}
.numberInput {
    width: 100%;
    .numberInputBox {
        display: inline-block;
        width: 76%;
        height: 36px;
        border: 1px solid #8f8f9d;
        vertical-align: top;
        input {
            border: none;
            width: 95%;
            height: 13px;
        }
    }
    .codeInput {
        display: inline-block;
        background-color: white;
        z-index: 5;
        .codeBtn {
            border: 1px solid #8f8f9d;
            border-right: none;
            padding: 6px 8px 8px 8px;
            width: 115px;
            height: 22px;
            color: #858585;
        }
    }
}
.categoryBox {
    width: 50%;
    vertical-align: top;
    select {
        width: 95%;
        padding: 10px;
        border: 1px solid #8f8f9d;
        background-color: white;
    }
}
.fileInput {
    display: inline-block;
    .fileName {
        border: 1px solid #dddddd;
        padding: 10px;
    }
    .condition {
        font-size: small;
    }
    input {
        display: inline-block;
        height: 40px;
        padding: 0 10px;
        vertical-align: middle;
        border: 1px solid #dddddd;
        width: 78%;
        color: #999999;
    }
    label {
        display: inline-block;
        color: black;
        padding: 10px;
        width: 525px;
        margin-bottom: 10px;
        border: 1px solid #dddddd;
        cursor: pointer;
    }
    input[type="file"] {
        position: absolute;
        width: 0;
        height: 0;
        padding: 0;
        overflow: hidden;
        border: 0;
    }
}
.reqButton {
    float: right;
    background-color: #ffe500;
    padding: 10px 55px;
    border: none;
    font-weight: bold;
    > div {
        display: inline-block;
        background-color: #e8e8e8;
        background: url("../../../public/ico.png") no-repeat -80px -60px;
        width: 17px;
        height: 17px;
        > a {
            text-decoration: none;
        }
    }
}
